import React from "react";
import PropTypes from 'prop-types'
import renderHTML from "react-render-html";

import { FadeTransition } from "./FadeTransition";

import { ReactComponent as IconMenu } from "../images/arrow-right.svg";



export const TestIntro = (props) => {
  return (
    <FadeTransition {...props}>
      <div className="test-wrapper--intro">
        <div className="test-wrapper--intro-illustration"></div>
        {renderHTML(props.intro)}
        <button onClick={props.start}>
          <span>
            Start testen <IconMenu width={27} height={15} />
          </span>
        </button>
      </div>
    </FadeTransition>
  );
};

TestIntro.propTypes = {
  intro: PropTypes.string.isRequired,
  start: PropTypes.func.isRequired,
};
  