import React, { Component } from "react";
import PropTypes from "prop-types";
import "../main.css";

// Components
import { Loading } from "./Loading";
import { TestIntro } from "./TestIntro";
import { Question } from "./Question";
import { Result } from "./Result";
import { Status } from "./Status";

import { historyRecord, historyGoBack } from "../includes/history";
import { jsonUrl } from "../includes/common";
import { STATE_INITIAL, STATE_TEST_STARTED } from "../includes/constants";

// eslint-disable-next-line no-undef
const axios = require("axios").default;
//------------------- DanmarkTesten ------------------------------

class DanmarkTesten extends Component {
  constructor(props) {
    super(props);

    if (props.json) {
      // test inject
      this.state = {
        ...STATE_INITIAL,
        ...{
          steps: props.json.questions[props.json.questions.length - 1].step + 1,
          count: props.json.questions.length + 1,
          json: props.json,
        },
      };
    } else {
      this.state = STATE_INITIAL;

      axios
        .get(jsonUrl(this.props.quiz))
        .then((res) => {
          var json = res.data.data;
          this.setState(() => {
            return {
              steps: json.questions[json.questions.length - 1].step + 1,
              count: json.questions.length + 1,
              json: json,
            };
          });
        })
        .catch((e) => {
          if (console.warn) console.warn(e);
        });
    }
  }

  setActualAge = (age) => {
    this.setState(() => {
      return { actualAge: age };
    });
  };

  startTest = () => {
    this.setState(() => {
      return { step: 1 };
    });
    historyRecord(this, STATE_TEST_STARTED);
  };

  recordAnswerAndStep = (answer, del_answers) => {
    let given_answers = this.state.given_answers.filter(
      (e) => !del_answers.includes(e)
    );

    given_answers.push(answer);

    let res = { given_answers };

    if (this.calcScore(given_answers) < 0) {
      res.step = this.state.count;

      historyRecord(this, {
        step: res.step,
        given_answers: res.given_answers,
      });
      this.setState(res);
      return;
    }

    let step = 0;

    let next_step = this.state.step + 1;
    if (next_step >= this.state.count) {
      step = this.state.count;
    }

    while (!step && next_step < this.state.count) {
      let question = this.state.json.questions[next_step - 1];

      let deps = question.depends || [];
      if (!deps.length) {
        step = next_step;
      } else {
        let met_deps = 0;
        deps.forEach((dep) => {
          given_answers.forEach((answer) => {
            if (dep === answer) {
              ++met_deps;
            }
          });
        });

        if (0 < met_deps) {
          if (question.operator && question.operator === "or") {
            step = next_step;
          }
          if (met_deps === deps.length) {
            step = next_step;
          }
        }
      }
      next_step++;
    }

    if (!step || step >= this.state.count) {
      step = this.state.count;
    }

    res.step = step;

    historyRecord(this, {
      step: res.step,
      given_answers: res.given_answers,
    });
    this.setState(res);
  };

  findScores = (scores, answers, first_answer) => {
    for (let i = 0; i < answers.length; ++i) {
      let a = answers[i];
      scores[a.answer_id] = Array.isArray(a.score)
        ? a.score[first_answer]
        : a.score;
    }
    return scores;
  };

  calcScore = (prelim_answers) => {
    if (!this.state.json) {
      return 0;
    }

    let given_answers = prelim_answers || this.state.given_answers;
    let questions = this.state.json.questions;
    let scores = [];
    let first_answer = given_answers[0] || 0;
    for (let i = 0; i < questions.length; ++i) {
      let q = questions[i];
      this.findScores(scores, q.answers, first_answer);
      if (q.sub) {
        q.sub.forEach((sub) => {
          this.findScores(scores, sub.answers, first_answer);
        });
      }
    }
    let score = given_answers.reduce((sum, i) => sum + scores[i], 0);
    return score;
  };

  getResult = () => {
    var score = this.calcScore();
    var results = this.state.json.results;
    for (var i = 0; i < results.length; ++i) {
      var res = results[i];
      if (
        (res.up_to && score < res.up_to) ||
        (res.in && res.in.includes(score)) ||
        (!res.in && !res.up_to)
      ) {
        return res;
      }
    }
    return {};
  };

  goBack = () => historyGoBack(this);

  isDebugging = () => this.state.json !== undefined && this.state.json.debug;

  render() {
    let status_step = 0;
    if (this.state.step)
      status_step =
        this.state.step >= this.state.count
          ? this.state.steps
          : this.state.json.questions[this.state.step - 1].step;

    if (this.isDebugging()) {
      console.group("Current state");
      console.log("History", this.state.history);
      console.log("Current score", this.calcScore());
      console.log("State", this.state);
      console.groupEnd("Current state");
    }

    return (
      <div className="test-wrapper">
        {1 < this.state.step && this.state.step < this.state.count && (
          <div className="button-back" onClick={this.goBack}>
            Tilbage
          </div>
        )}
        <div className="test-wrapper-body">
          {0 === this.state.step && !this.state.json && (
            <Loading key="loading" />
          )}
          {0 === this.state.step && this.state.json && (
            <TestIntro
              key="testintro"
              intro={this.state.json.intro}
              start={this.startTest}
            />
          )}
          {0 < this.state.step && this.state.step >= this.state.count && (
            <Result key="result" result={this.getResult()} />
          )}
          {0 < this.state.step && this.state.step < this.state.count && (
            <Question
              step={this.state.step}
              key={"question" + this.state.step}
              question={this.state.json.questions[this.state.step - 1]}
              given_answers={this.state.given_answers}
              recordAnswerAndStep={this.recordAnswerAndStep}
              setActualAge={this.setActualAge}
              actualAge={this.state.actualAge}
            />
          )}
          {0 < this.state.step && (
            <Status key="status" step={status_step} count={this.state.steps} />
          )}
        </div>
      </div>
    );
  }
}

DanmarkTesten.propTypes = {
  quiz: PropTypes.string,
  json: PropTypes.object,
};

export default DanmarkTesten;
