import React from "react";
import PropTypes from "prop-types";
import { FadeTransition } from "./FadeTransition";
import { TestStep } from "./TestStep";

export const Question = (props) => {
  var step = props.step;
  var question = props.question;
  var given_answers = props.given_answers;
  var recordAnswerAndStep = props.recordAnswerAndStep;
  let onClick = (add_answer, del_answers) => {
    recordAnswerAndStep(add_answer, del_answers);
  };

  return (
    <FadeTransition {...props}>
      <div className="Question">
        <TestStep
          step={step}
          info={question}
          onClick={onClick}
          answered={given_answers}
          setActualAge={props.setActualAge}
          actualAge={props.actualAge}
        />
      </div>
    </FadeTransition>
  );
};
Question.propTypes = {
  question: PropTypes.object.isRequired,
  given_answers: PropTypes.array.isRequired,
  recordAnswerAndStep: PropTypes.func.isRequired,
  step: PropTypes.number.isRequired,
  setActualAge: PropTypes.func.isRequired,
  actualAge: PropTypes.number.isRequired
};
