/* eslint-disable react/prop-types */
import React, { Component } from "react";
import renderHTML from "react-render-html";
import { siteImproveEventPush } from "../helpers/siteImproveEventPush";

//------------------- AgeStep ------------------------------

export class AgeStep extends Component {
  constructor(props) {
    super(props);

    let intervals = [];
    props.answers.forEach((answer) => {
      let res = answer.text.split("-");
      intervals.push({
        from: res[0],
        to: res[1],
        id: answer.answer_id,
      });
    });
    const answer_ids = props.answers.map((a) => a.answer_id);
    this.state = { age: "", intervals, answer_ids, showMessage: false };
  }

  componentDidMount = () => {
    siteImproveEventPush(
      null,
      "danmark testen",
      "Load Age step",
      "User continued to age step"
    );
  };

  handleChange = (event) => {
    this.setState({
      age: parseInt(event.target.value),
      showMessage: false,
    });
  };

  handleNext = () => {
    const age = this.state.age;
    this.props.setAge(age);

    if (age < 16 || age > 59) {
      this.setState({ showMessage: true });
      siteImproveEventPush(
        null,
        "danmark testen",
        "Age input error",
        "User gets an error on enter age step"
      );
      return;
    }

    let answer = this.state.intervals.find((element) => {
      return age >= element.from && age <= element.to;
    });

    this.props.nextStep(answer.id, this.state.answer_ids);
  };

  render() {
    const buttonClass = this.state.showMessage ? "next disabled" : "next";
    return (
      <div className="age-step">
        <label htmlFor="age">Indtast alder:</label>
        <input
          name="age"
          type="number"
          value={this.state.age}
          onChange={this.handleChange}
        />
        <button className={buttonClass} onClick={() => this.handleNext()}>
          Næste
        </button>
        {this.state.showMessage && this.props.message && (
          <div className="message">{renderHTML(this.props.message)}</div>
        )}
      </div>
    );
  }
}
