import React from "react";
import PropTypes from "prop-types";
import renderHTML from "react-render-html";

import { AgeStep } from "./AgeStep";
import { filter, includes } from "lodash";

export const TestStep = (props) => {
  const { step, info, actualAge, setActualAge } = props;
  const { onClick, answered } = props;

  const age = answered.length > 0 ? answered[0] : null;
  const filteredAnswers = filter(
    info.answers,
    function (answer) {
      return (
        answer.age === undefined ||
        answer.age === null ||
        includes(answer.age, age)
      );
    },
    age
  );
  const questionText = info.text.filter((questionText) => {
    if (questionText.minAge === null && questionText.maxAge === null)
      return true;
    if (actualAge >= questionText.minAge && actualAge <= questionText.maxAge)
      return true;

    return false;
  })[0]?.text;
  var answers_for_this_question = filteredAnswers.map((a) => a.answer_id);

  const setAge = (age) => {
    setActualAge(age);
  };

  return (
    <div className="test-wrapper--step">
      <h2>{info.question}</h2>
      {questionText && renderHTML(questionText)}

      {step === 1 && (
        <AgeStep
          answers={info.answers}
          nextStep={onClick}
          message={info.message}
          setAge={setAge}
        />
      )}

      {step > 1 &&
        filteredAnswers.map((answer) => {
          var className;
          if (answered.includes(answer.answer_id)) className = "active";
          return (
            <div key={answer.answer_id}>
              <button
                className={className}
                onClick={onClick.bind(
                  null,
                  answer.answer_id,
                  answers_for_this_question
                )}
              >
                {answer.text}
              </button>
            </div>
          );
        })}
      {info.extra && renderHTML(info.extra)}
    </div>
  );
};

TestStep.propTypes = {
  info: PropTypes.object.isRequired,
  step: PropTypes.any,
  onClick: PropTypes.func.isRequired,
  answered: PropTypes.array.isRequired,
  setActualAge: PropTypes.func.isRequired,
  actualAge: PropTypes.number.isRequired,
};
