import React from "react";
import PropTypes from "prop-types";

import { FadeTransition } from "./FadeTransition";
import { ResultPage } from "./ResultPage";

export function Result(props) {
  const { result } = props;
  const resultClass = `result result__group--${result.group}`;

  return (
    <FadeTransition {...props}>
      <div className="test-wrapper--result">
        <ResultPage
          className={resultClass}
          markup={result.html}
          group={result.group}
          primaryGroupName={result.primaryGroupName}
          secondaryGroupName={result.secondaryGroupName}
          pris={result?.pris}
          primaryButtonText={result.primaryButtonText}
          primaryButtonLink={result.primaryButtonLink}
          secondaryButtonText={result.secondaryButtonText}
          secondaryButtonLink={result.secondaryButtonLink}
        />
      </div>
    </FadeTransition>
  );
}
Result.propTypes = {
  result: PropTypes.object.isRequired,
};
